.adv-toggle {
  background: #d3a47c;
  padding-bottom: 20px;
}

.adv-toggle__arrow-block {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  height: 35px;
  width: 35px;
  border: 2px solid #fff;
  border-radius: 50%;
}

.adv__toggle__arrow {
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: center;
  align-items: center;
  height: 35px;
  width: 35px;
}

.adv__toggle__left {
  position: absolute;
  background: #fff;
  width: 15px;
  height: 3px;
  transform: rotate(45deg);
  border-radius: 6px;
  right: 15px;
  top: 17px;
}

.adv__toggle__right {
  position: absolute;
  background: #fff;
  width: 15px;
  height: 3px;
  transform: rotate(-45deg);
  border-radius: 6px;
  left: 15px;
  top: 17px;
}

.advg {
  background: #d3a47c;
}

.advg__toggle-arrow-block {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  height: 35px;
  width: 35px;
  border: 2px solid #fff;
  border-radius: 50%;
}

.advg__toggle-arrow {
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: center;
  align-items: center;
  height: 35px;
  width: 35px;
  transition: all 0.3s;
  cursor: pointer;
  border-radius: 50%;
}

.advg__toggle-arrow:hover {
