}

.advg__arrow-anim {
  transition: all 0.3s;
  transform: rotate(180deg);
}

.advg__toggle-left {
  position: absolute;
  background: #fff;
  width: 15px;
  height: 3px;
  transform: rotate(45deg);
  border-radius: 6px;
  right: 15px;
  top: 17px;
}

.advg__toggle-right {
  position: absolute;
  background: #fff;
  width: 15px;
  height: 3px;
  transform: rotate(-45deg);
  border-radius: 6px;
  left: 15px;
  top: 17px;
}

.advg__toggled {
  padding: 0;
  margin-top: 45px;
  background: #436276 url(../img//content/fon.png) 0% 0%;
  transition: all 2s;
}

.advg__toggled:before {
  content: '';
